// eslint-disable-next-line import/no-extraneous-dependencies
import { Lightbox } from 'react-modal-image';
import { removePhoto } from './ModalImageSlice';
import { useAppDispatch } from '../../app/hooks';

type Props = {
  image: string,
  alt: string,
};

export const ModalImage: React.FC<Props> = ({ image, alt }) => {
  const dispatch = useAppDispatch();

  return (
    <Lightbox
      small={image}
      large={image}
      alt={alt}
      hideDownload
      onClose={() => dispatch(removePhoto())}
    />
  );
};
