/* eslint-disable jsx-a11y/click-events-have-key-events */
/* eslint-disable jsx-a11y/no-noninteractive-element-interactions */
import { Navigation, Pagination } from 'swiper';
import { Swiper, SwiperSlide } from 'swiper/react';

import 'swiper/css';
import './Slider.scss';
import { setPhoto } from '../ModalImage/ModalImageSlice';
import { useAppDispatch } from '../../app/hooks';

type Props = {
  images: [string, string][],
};

export const Slider: React.FC<Props> = ({ images }) => {
  const dispatch = useAppDispatch();

  return (
    <Swiper
      className="Slider"
      modules={[Navigation, Pagination]}
      effect="fade"
      spaceBetween={0}
      loop
      navigation={{
        nextEl: '.Slider__next',
        prevEl: '.Slider__prev',
      }}
      pagination={{
        clickable: true,
        type: 'bullets',
      }}
    >
      {images.map(image => (
        <SwiperSlide
          key={image[0]}
          className="Slider__slide"
        >
          <img
            src={image[0]}
            alt={image[0]}
            className="Slider__image"
            onClick={() => {
              dispatch(setPhoto({ image: image[0], alt: image[1] }));
            }}
          />
        </SwiperSlide>
      ))}

      <div className="Slider__prev swiper-button-prev" />
      <div className="Slider__next swiper-button-next" />

    </Swiper>
  );
};
