import { useEffect, useState } from 'react';
import { Routes, Route } from 'react-router-dom';
import './App.scss';
import { Footer } from './features/Footer';
import { Header } from './features/Header';
import { Home } from './pages/Home';
import { About } from './pages/About';
import { Anchor } from './features/Anchor';
import { ModalImage } from './features/ModalImage';
import { useAppSelector } from './app/hooks';

const App: React.FC = () => {
  const modalImage = useAppSelector(state => state.modalImage.photo);
  const [scrollTop, setScrollTop] = useState(0);

  // eslint-disable-next-line no-console
  console.log(modalImage);

  useEffect(() => {
    const handleScroll = () => {
      setScrollTop(window.scrollY);
    };

    window.addEventListener('scroll', handleScroll);

    return () => {
      window.removeEventListener('scroll', handleScroll);
    };
  }, [window]);

  return (
    <div className="App">
      <div className="App__section App__section--header">
        <Header />
      </div>

      <div className="App__section App__section--main container-xl">
        <Routes>
          <Route path="/products?/:page?/*" element={<Home />} />
          <Route path="about" element={<About />} />
        </Routes>
      </div>

      <div className="App__Anchor" hidden={!scrollTop}>
        <Anchor />
      </div>

      {modalImage && (
        <ModalImage image={modalImage.image} alt={modalImage.alt} />
      )}

      <div className="App__section App__section--footer">
        <div className="App__wrapper container-xl">
          <Footer />
        </div>
      </div>
    </div>
  );
};

export default App;
