/* eslint-disable no-param-reassign */
import {
  PayloadAction,
  createSlice,
} from '@reduxjs/toolkit';

export interface ModalImageState {
  photo: {
    image: string,
    alt: string,
  } | null
}

const initialState: ModalImageState = {
  photo: null,
};

export const modalImage = createSlice({
  name: 'modalImage',
  initialState,
  reducers: {
    setPhoto(state, action: PayloadAction<typeof state.photo>) {
      state.photo = action.payload;
    },
    removePhoto(state) {
      state.photo = null;
    },
  },
});

export const { setPhoto, removePhoto } = modalImage.actions;

export default modalImage.reducer;
